 body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #F4F2EA;
  width: 100%; 
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@media only screen and (max-width: 800px) {
 .App {
  display: flex;
  flex-direction: column;
 }
    .left {
    
      background: #F4F2EA;
      width: 100%;
      height: 100vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    
    .right {
      background: #BEB7AC;
      width: 100%;
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      justify-content: center;
    }
   

   
    
        
        .logo {
          /* margin-top: 200px; */
          font-size: 200px;
          color: #A97A50;
     
          font-family: 'hippie-mojo';
  
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
      }
    
      .slogan {
        font-family: 'ITC Souvenir Std';
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        margin-bottom: 4rem;
        color:#A7784f;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
      }
 

        #background-video {
          height: 100%; 
          width: 100%;
          position: absolute;
          object-fit: cover;
         
        }

        .sign-up {

          display: flex;
          border: 1px solid #f8f7f4;
          border: none;
          padding: 2px 4px;
          border-radius: 20px;
          margin-top: .6rem;
          margin-top: 2.5rem;
          /* background: #e9e5dc;  */
          height: 2.7rem;
          width: 15rem;
          color: #B7B1A7;
       
          font-family: 'Helvetica Neue';
          font-size: 19px;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          /* background: #e9e5dc; */
          background: none;
          padding: 0px 14px;
      }

      .sign-up:hover {
        background: none; 
        color: grey;

    }

    .suggestion-input {
      background: #fafafa;
      border: hidden;
      text-decoration: none;
      height: 2.2rem;
      outline: none;
      background: none;
      border-radius: 0 9px 9px 0;
      padding-left: 0.8rem;
      width: 20rem;
      font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    }
     

    .suggestion-form {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      border: 1px solid #f8f7f4;
      border: none;
      padding: 2px 4px;
      border-radius: 20px;
      margin-top: .6rem;
      margin-top: 3rem;
      background: #e9e5dc; 
    }

    .suggestion-form-content {
      display: flex;
      flex-direction: row;
      align-items: center;
    }
    
    
 
}


